import comHeader from "../../components/com-header.vue";
export default {
	data() {
		return {
			taskName: "",
			count: 0
		}
	},
	components: {
		comHeader
	},
	methods: {
		handleInput(e) {
			this.taskName = e.detail.value;
			this.count = this.taskName.length;
		},
		handleBtnClick() {
			if (this.count == 0) {
				uni.showToast({
					title:"不能为空!",
					icon: "none"
				});
			} else {
				this.$bus.$emit("task-add", this.taskName);
				uni.switchTab({
					url: "./index"
				});	
			}
		}
	}
}
